/*  Ask a Derm Page
*
*  Version:
*    1.0.0 - 2022/01/20
*/

.c-ask-derm {
  position: relative;
  padding-top: 7rem;
  z-index: 0;

  &__header-background {
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    padding-bottom: 28.25rem;
    background-color: $s-color--background-tertiary;
    z-index: -1;
  }

  &__content-container {
    text-align: center;
  }

  &__title {
    margin-top: 1.4375rem;
    color: $s-color--font-6;
  }

  &__content {
    text-align: center;
    max-width: 26rem;
    width: 82%;
    margin: 2.5rem auto 0;
    line-height: 1.125rem;
  }

  &__images-wrapper {
    &.is-desktop {
      display: none;
    }

    margin-top: 2.8125rem;
    position: relative;
    max-width: 54.0625rem;
  }

  &__image-container {
    padding-bottom: 101%;
  }

  &__image-overlay {
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(1.875rem);
    inset: 11%;
    position: absolute;
  }

  &__ask {
    text-align: center;
    margin: 3.0625rem auto 2.8125rem;
    max-width: 47.25rem;
    width: 85%;

    &-success {
      margin: 2.5rem auto 3.75rem;
      text-align: center;
      @include t-gutters;
    }

    &-content {
      margin: 1rem auto 2.8125rem;
      width: 89%;
      line-height: 1.125rem;
    }

    &-input {
      border-bottom: 0.0625rem solid $s-color--border-secondary;
      display: block;
      padding: 0.625rem 0.625rem 1.0625rem 0.625rem;
      width: 100%;
    }

    &-submit {
      margin-top: 1.25rem;
      margin-left: auto;

      &.o-btn {
        display: block;
      }
    }
  }

  &__counter {
    margin-top: 5.4375rem;
  }

  @media screen and (min-width: 34.375rem) {
    &__images-wrapper {
      margin-left: auto;
      margin-right: auto;
      width: 80%;
    }
  }

  @include t-media-query($s-small-up) {

    &__images-wrapper {
      width: 60%;
    }
  }

  @include t-media-query($s-medium-up) {
    &__header-background {
      padding-bottom: 31.25rem;
    }

    &__hero {
      margin-top: 6.25rem;
      margin-bottom: 10rem;
      padding: 0 3.125rem;
      display: flex;
      justify-content: flex-end;

      &-wrapper {
        width: 50%;
      }
    }

    &__content-container {
      text-align: left;
    }

    &__content {
      text-align: left;
      margin-left: 0;
      line-height: 1.625rem;
    }

    &__images-wrapper {
      height: calc(50vw - 3.125rem);
      width: 50%;
      margin: 0;

      &.is-desktop {
        display: block;
      }

      &.is-mobile {
        display: none;
      }
    }

    &__ask {
      text-align: left;
      margin: 6.0625rem 0 0;

      &-success {
        text-align: left;
        margin: 4.375rem 0;
      }

      &-content {
        margin-left: 0;
        line-height: 1.8125rem;
      }
    }

    &__counter {
      margin-top: 9.375rem;
    }
  }

  @include t-media-query($s-large-up) {
    &__ask {

      &-success {
        margin: 6.25rem 0;
        width: 80%;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__header-background {
      padding-bottom: 32.25rem;
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__hero {
      padding-left: 8.75rem;
      padding-right: 8.75rem;
      margin-top: 5.75rem;

      &-wrapper {
        width: 49.8%;
      }
    }

    &__header-background {
      padding-bottom: 35.5rem;
    }

    &__image-container {
      padding-bottom: 104%;
    }

    &__images-wrapper {
      height: initial;
    }

    &__image-wrapper {
      width: 51%;
    }

    &__image-overlay {
      inset: 9%;
    }

    &__content-container {
      margin-top: 0.25rem;
    }

    &__title {
      margin-top: 2.125rem;
    }

    &__content {
      margin-top: 1.125rem;
    }

    &__ask {
      margin-top: 9.9375rem;

      &-success {
        margin: 7.5rem 0;
      }

      &-content {
        margin-top: 2.5625rem;
        margin-bottom: 3.9375rem;
        width: 88%;
      }

      &-submit {
        margin-top: 1.6875rem;
        width: 16.875rem;
      }
    }

    &__counter {
      margin-top: 16.125rem;
    }
  }
}