.o-countdown {
  &__text {
    text-align: center;

    &.is-hidden {
      display: none;
    }
  }

  &__title {
    max-width: 39rem;
    width: 86%;
    margin: 0 auto;
  }

  &__date {
    width: 1em;
    text-align: center;
    flex-shrink: 0;
    color: $s-color--font-6;
    @include t-font--heading-4;

    &-title {
      width: 3em;
      display: block;
      @include t-font--type-7;
      color: $s-color--font-6;
      text-transform: capitalize;

      &-container {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }

    &-container {
      margin-top: 0.8125rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-colon {
      color: $s-color--font-6;
      @include t-font--heading-3;
    }
  }

  &__subtitle {
    color: $s-color--font-6;
    max-width: 27.3125rem;
    width: 65%;
    margin: 1.125rem auto 0;
    text-align: center;
  }

  @include t-media-query($s-large-up) {
    &__date {
      @include t-font--heading-3;
    }

    &__subtitle {
      margin-top: 2.9375rem;
    }

    &__title {
      width: 80%;
    }
  }
}